<template>
   <el-card shadow='hover'>
      <el-form ref="form" :model="form" :rules="rules" label-width="150px" style="width:50%;margin-top:30px">
        <el-form-item label="原问题">
           {{user_question}}
        </el-form-item>
        <el-form-item label="原问题答案" prop="user_answer_old" style="margin-bottom:30px">
            <el-input v-model="form.user_answer_old"></el-input>
        </el-form-item>
        <el-form-item label="验证问题" prop="user_question" style="margin-bottom:30px">
            <el-input v-model="form.user_question"></el-input>
        </el-form-item>
        <el-form-item label="问题答案" prop="user_answer" style="margin-bottom:30px">
            <el-input v-model="form.user_answer"  @keyup.enter.native='onSubmit'></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" :loading="loading"  @click.native.prevent="onSubmit">确定</el-button>
        </el-form-item>
        </el-form>
  </el-card>
</template>
<script>
import { md5Encryption } from "@/utils";
export default {
    name: 'ValidProblem',
    data() {
      return {
        user_question: this.$store.state.user.user.user_question,
        form: {
          user_question: '',
          user_answer: '',
          user_answer_old: ''
        },
        loading: false,
        rules: {
          user_answer_old: [
            { required: true, message: '请输入原问题答案', trigger: 'blur' }
          ],
          user_question: [
            { required: true, message: '请输入验证问题', trigger: 'blur' }
          ],
          user_answer: [
            { required: true, message: '请输入确认新密码', trigger: 'blur' }
          ],
      
        }
      }
    },
    methods: {
      onSubmit() {
        this.$refs.form.validate(valid => {
          if (valid) {
             const { user_answer_old, user_question,user_answer} = this.form;
              this.loading = true
              const data = {
                user_answer_old:md5Encryption(user_answer_old),
                user_answer:md5Encryption(user_answer),
                user_question
              }
              this.$store.dispatch('user/changeqa',data).then(()=>{
                this.$refs.form.resetFields()
                this.loading = false
                 this.$store.dispatch('message/hasnew')
                this.$message({
                      message: '修改验证问题成功！',
                      type: 'success'
                  });
              }).catch(()=>{
                this.loading = false
              })
           
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
}
</script>

